.ul, .text-style ul {
  list-style: none;
  padding-left: 0; }
  .ul li, .text-style ul li {
    padding-left: 20px;
    position: relative; }
    .ul li + li, .text-style ul li + li, .ul li ol, .text-style ul li ol, .ul li ul, .text-style ul li ul {
      margin-top: 20px; }
    .ul li::before, .text-style ul li::before {
      content: "";
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: #426cff;
      position: absolute;
      left: 0;
      top: 4px; }

.ol, .text-style ol {
  list-style: none;
  counter-reset: item;
  padding-left: 0; }
  .ol li, .text-style ol li {
    display: block;
    position: relative;
    padding-left: calc(0.8em + 10px); }
    .ol li + li, .text-style ol li + li, .ol li ol, .text-style ol li ol, .ol li ul, .text-style ol li ul {
      margin-top: 20px; }
    .ol li::before, .text-style ol li::before {
      content: counters(item, ".") ".";
      counter-increment: item;
      color: #426cff;
      font-weight: 500;
      position: absolute;
      left: 0;
      top: 0; }
    .ol li > ul > li, .text-style ol li > ul > li, .ol li ol > li, .text-style ol li ol > li {
      padding-left: 1em; }
      .ol li > ul > li::before, .text-style ol li > ul > li::before, .ol li ol > li::before, .text-style ol li ol > li::before {
        left: calc(-0.8em - 10px); }
      .ol li > ul > li > ul li, .text-style ol li > ul > li > ul li, .ol li > ul > li ol li, .text-style ol li > ul > li ol li, .ol li ol > li > ul li, .text-style ol li ol > li > ul li, .ol li ol > li ol li, .text-style ol li ol > li ol li {
        padding-left: 1em; }
        .ol li > ul > li > ul li::before, .text-style ol li > ul > li > ul li::before, .ol li > ul > li ol li::before, .text-style ol li > ul > li ol li::before, .ol li ol > li > ul li::before, .text-style ol li ol > li > ul li::before, .ol li ol > li ol li::before, .text-style ol li ol > li ol li::before {
          left: calc(-0.8em*2 - 10px); }

